.social-icon {
  height: 20px;
  width: 20px;
  padding-top: 5px;
  padding-inline: 8px;
  filter: var(--invert-filter);

  &:hover {
    filter: $redfilter;
    top: -2px;
  }
}

.cell-icon {
  top: 5px;
  height: 30px;
  padding-right: 20px;
  width: auto;
  display: inline;
  position: relative;
}

.dark-invert {
  filter: var(--invert-filter);
}
